.icon {
    width: 50px;
    height: 50px;
    transition: .3s linear;
}

.icon:hover {
    transform: scale(1.2);
    cursor: pointer;
    transition: .3s linear;
}

@media (max-width: 440px) {
    .iconContainer {
        display: flex;
        justify-content: center;
        min-width: 50%;
    }
}

.mail {
    margin: 20px auto 0;
}